    to bottom right,
    rgba(0, 181, 23, 0.9),
    rgba(33, 37, 41, 0.4)
  );
  /* Standard syntax */
  transition: 0.5s;
}

.bg-gradient-orange {
  background: rgba(33, 37, 41, 0.4);
  /* For browsers that do not support gradients */
  /* For Safari 5.1 to 6.0 */
  /* For Opera 11.1 to 12.0 */
  /* For Firefox 3.6 to 15 */
