    to bottom right,
    rgba(255, 144, 23, 0.9),
    rgba(33, 37, 41, 0.4)
  );
  /* Standard syntax */
  transition: 0.5s;
}

.bg-gradient-red {
  background: rgba(33, 37, 41, 0.4);
  /* For browsers that do not support gradients */
  /* For Safari 5.1 to 6.0 */
  /* For Opera 11.1 to 12.0 */
  /* For Firefox 3.6 to 15 */
  background: linear-gradient(
    to bottom right,
    rgba(250, 52, 52, 0.9),
    rgba(33, 37, 41, 0.4)
  );
  /* Standard syntax */
  transition: 0.5s;
}

.bg-gradient-pink {
  background: rgba(33, 37, 41, 0.4);
  /* For browsers that do not support gradients */
  /* For Safari 5.1 to 6.0 */
  /* For Opera 11.1 to 12.0 */
  /* For Firefox 3.6 to 15 */
  background: linear-gradient(
    to bottom right,
    rgba(232, 62, 140, 0.9),
